/************sectin login*************/
.section_login {
    width: 100%;
}

.section_login .login_wrap {
    display: flex;
    justify-content: center;
    margin-top: 50px;


}

.section_login .login_wrap li {
    width: 200px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    background-color: #fff;
    border: 1px solid #ccc;
}

.section_login .login_wrap li:last-child {
    border-left: 0;

}

.section_login .login_wrap li label {
    display: block;
    width: 100%;
    font-weight: 550;
    /* color: #fff; */
    position: relative;
}

.section_login .login_wrap li label::after {
    content: "";
    position: absolute;
    left: 50%;
    bottom: 0px;
    width: 0;
    height: 4px;
    background-color: #fff;
    transition: all .3s;
}

.section_login .login_wrap li label::before {
    content: "";
    position: absolute;
    right: 50%;
    bottom: 0px;
    width: 0;
    height: 4px;
    background-color: #fff;
    transition: all .3s;
}

