/************popup*************/
.pop {
    width: 250px;
    height: 100px;
    border: 1px solid #ccc;
    border-radius: 10px;
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #eee;
}

.pop>p {
    text-align: center;
    margin-top: 10px;

}

.cbtn {
    width: 80px;
    height: 25px;
    margin: 0 auto;
    margin-top: 20px;
    border: 1px solid #ccc;
    line-height: 25px;
    cursor: pointer;
    background-color: #002053;
    color: #fff;
    transition: .3s;
}

.cbtn:hover {
    box-shadow: 0 0 5px black;
}


.id_pw_info {
    width: 250px;
    height: 100px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #fff;
    text-align: center;
    font-size: 14px;
    position: absolute;
    top: 35%;
    right: 20%;
    animation: idinfo 1s infinite linear;
}

.id_pw_info button {
    width: 50px;
    height: 30px;
    background-color: #002053;
    color: #fff;
    outline: none;
    border: 1px solid #ccc;
    cursor: pointer;
    margin-top: 10px;
}

.id_pw_info button:hover {
    box-shadow: 0 0 5px black;
}

@keyframes idinfo {
    0% {
        top: 36%;
    }

    50% {
        top: 35%;
    }

    100% {
        top: 36%;
    }
}


.find {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    z-index: 99999;
    

}
.find_01{
    display: none;
}
.find_02{
    display: none;
}

.find_ex {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
}

.find_inner {
    width: 450px;
    height: 400px;

    background-color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.find_inner .findtit {
    width: 300px;
    height: 50px;
    margin: 20px auto;
    border: 1px solid #ccc;
    font-size: 22px;
    text-align: center;
    line-height: 50px;
    background-color: #238ece;
    color: #fff;
}

.find_inner label {
    display: block;
    width: 300px;
    margin: 0 auto;
    margin-bottom: 10px;
}

.find_inner input {
    display: block;
    width: 300px;
    height: 35px;
    margin: 0 auto;
    margin-bottom: 20px;
    outline: none;

}

.find_inner button {
    display: block;
    width: 300px;
    height: 35px;
    margin: 0 auto;
    background-color: #fff;
    border: 1px solid #ccc;
    margin-top: 30px;

}

.find_inner button:hover {
    cursor: pointer;
    box-shadow: 0 0 10px black;
}
button.findclose{
    margin-top: 20px;
}

.idbox{
    width: 300px;
    height: 35px;
    border: 1px solid #ccc;
    margin: 0 auto;
    text-align: center;
    line-height: 35px;

}
